<template>
  <div ref="wrapper" class="cpc-base-scroll">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'

  export default {
    props: {
      /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
      probeType: {
        type: Number,
        default: 1
      },
      //click
      click: {
        type: Boolean,
        default: true
      },
      //
      scrollX: {
        type: Boolean,
        default: false
      },
      //
      scroll: {
        type: Boolean,
        default: false
      },
      //
      touchEnd: {
        type: Boolean,
        default: false
      },
      //
      beforeScrollStart: {
        type: Boolean,
        default: false
      },
      //
      scrollEnd: {
        type: Boolean,
        default: false
      },
      //
      pullDownRefresh: {
        type: [Boolean, Object],
        default: false
      }
    },
    mounted() {
      this.$nextTick(() => {
        this._initScroll()
      })
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        // better-scroll
        this.BaseScroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          pullDownRefresh: this.pullDownRefresh,
        })
        // 
        if (this.scroll) {
          let me = this
          this.BaseScroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }
        // 
        if (this.scrollEnd) {
          this.BaseScroll.on('scrollEnd', (pos) => {
            this.$emit('scrollEnd', pos)
          })
        }
        //
        if (this.touchEnd) {
          this.BaseScroll.on('touchEnd', (pos) => { // 
            this.$emit('touchEnd', pos)
          })
        }
        // 
        if (this.beforeScrollStart) {
          this.BaseScroll.on('beforeScrollStart', (pos) => {
            this.$emit('beforeScrollStart', pos)
          })
        }
      },
      disable() {
        // better-scrolldisable
        this.BaseScroll && this.BaseScroll.disable()
      },
      enable() {
        // better-scrollenable
        this.BaseScroll && this.BaseScroll.enable()
      },
      refresh() {
        // better-scrollrefresh
        this.BaseScroll && this.BaseScroll.refresh()
      },
      scrollTo() {
        // better-scrollscrollTo
        this.BaseScroll && this.BaseScroll.scrollTo.apply(this.BaseScroll, arguments)
      },
      scrollToElement() {
        // better-scrollscrollToElement
        this.BaseScroll && this.BaseScroll.scrollToElement.apply(this.BaseScroll, arguments)
      },
      finishPullDown() {
        // better-scrollscrollToElement
        this.BaseScroll && this.BaseScroll.finishPullDown.apply(this.BaseScroll, arguments)
      },
      finishPullUp() {
        // better-scrollscrollToElement
        this.BaseScroll && this.BaseScroll.finishPullUp.apply(this.BaseScroll, arguments)
      }
    }
  }
</script>
<style scoped lang="less">
  .cpc-base-scroll {
    height: 100%;
    overflow: hidden;
    .content {
      position: relative;
      min-height: calc(~"100% + 1px");
    }
  }
</style>



