<template>
  <swiper :dir="dir" id="banner" ref="swiper" :options="getOpeions">
    <swiper-slide v-for="item in datas">
      <img class="bannerImg" :src="item.img" :data-bindurl="item.url">
    </swiper-slide>
    <div :style="{'text-align': dotAlign}" class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script type="text/ecmascript-6">
  export default {
    components: {},
    data() {
      return {}
    },
    props: {
      datas:{
        type:Array,
        default:[]
      },
      /*ltr:,rtl:*/
      dir:{
        type:String,
        default:"ltr"
      },
      /*(horizontal)(vertical)*/
      direction:{
        type:String,
        default:'horizontal'
      },
      /* 
        "slide"'slide',
        "fade"
        "cube"
        "coverflow"3d
        "flip"3d
      */
      effect: {
        type: String,
        default: "slide"
      },
      /*  */
      delay:{
        type:Number,
        default:3000
      },
      /* page */
      clickable:{
        type:Boolean,
        default:false
      },
      /*swiperautoplay*/
      disableOnInteraction:{
        type:Boolean,
        default:false
      },
      /**/
      loop:{
        type:Boolean,
        default:true
      },
      /**/
      dynamicBullets:{
        type:Boolean,
        default:false
      },
      /*
        bullets  
        fraction  
        progressbar  
      */
      dotType:{
        type:String,
        default:'bullets'
      },
      /*left center right*/
      dotAlign:{
        type:String,
        default:"center"
      },
      /*,auto*/
      slidesPerView:{
        type:[Number,String],
        default:1
      },
      /**/
      spaceBetween:{
        type:Number,
        default:0
      },
      /*trueactive slide*/
      centeredSlides:{
        type:Boolean,
        default:false
      },
      /*slidewrappertruefreeslide*/
      freeMode:{
        type:Boolean,
        default:false
      },
    },
    methods: {
      changeActive(e) {
        let url = e.target.dataset.bindurl;
        this.$router.push(url)
      }
    },
    computed: {
      getOpeions() {
        let options = {
          effect: this.effect,
          autoplay: {
            delay: this.delay,
            disableOnInteraction: this.disableOnInteraction
          },
          on: {
            tap: this.changeActive
          },
          freeMode:this.freeMode,
          centeredSlides: this.centeredSlides,
          direction:this.direction,
          loop:this.loop,
          slidesPerView: this.slidesPerView,
          spaceBetween: this.spaceBetween,
          pagination: {
            el: '.swiper-pagination',
            clickable: this.clickable,
            dynamicBullets: this.dynamicBullets,
            type: this.dotType,
          }
        }
        return options;
      }
    },
    mounted() {

    },
    destroyed() {

    }
  }
</script>

<style lang="less" scoped>
  #banner {
    background-color: #fff;
    height: 1.5rem;
    .swiper-pagination {
      padding-right: 0.1rem;
    }
    .bannerImg {
      width: 100%;
      height: 1.5rem;
    }
  }
</style>



