<template>
  <cpcBaseRefresh ref="refreshScroll" @loading="loading" @loadMore="loadMore">
    <div slot="statePulldown">
      <img width="50px" height="50px" src="../../../assets/logo.png" alt="">
      
    </div>
    <div slot="stateRelease">
      <img width="50px" height="50px" src="../../../assets/logo.png" alt="">
      
    </div>
    <div slot="stateRefreshing"></div>
    <div slot="stateRefreshEnd"></div>
    <button @click="$router.goBack()"></button>
    <p v-for="item in lll">{{item}}new</p>
    <loading slot="loadMoreing"></loading>
    <div slot="loadMores"></div>
    <div slot="loadMoreALL"></div>
  </cpcBaseRefresh>
</template>

<script>
  import cpcBaseRefresh from '../../../CPCUI/refresh/cpcBaseRefresh.vue'
  import loading from '../../../CPCUI/animate/loading/cpcLoading.vue';

  export default {
    components: {cpcBaseRefresh,loading},
    name: 'HelloWorld',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        lll: [
          "", "", "", "", "", "", "", "", "", "",
          "", "", "", "", "", "", "", "", "", "",
          "", "", "", "", "", "", "", "", "", "",
          "", "", "", "", "", "", "", "", "", "",
          "", "", "", "", "", "", "", "", "", "",
          "", "", "", "", "", "", "", "", "", "",

        ],
      }
    },
    methods: {
      loading(finishPullDown) {
        setTimeout(() => {
          let arr = new Array(5)
          this.lll.unshift(...arr)
          finishPullDown();
        }, 2000)
      },
      loadMore(finishpullUp,setLoadMoreALL) {
        setTimeout(() => {
          let arr = new Array(5)
          this.lll.push(...arr)
          finishpullUp();
          if (arr.length == 0) {
            setLoadMoreALL(true);
          }
        }, 2000)
      }
    }
  }
</script>

<style scoped>

</style>



